import React from "react";
import './LuzGeral.css';
import { useNavigate } from "react-router-dom";

const Home = () => {
    const navigate = useNavigate();
    const handleClose = () => {
        navigate('/home');
    };

    return (
        <div className="luz-geral">
            <div className="lg-title-container">
                <div className="lg-title">
                    <p className="lg-title-text">
                       Um pouco mais sobre luz geral...
                    </p>
                </div>

                <div
                    onClick={handleClose} 
                    className="lg-button">
                    <p></p>
                </div>
            </div>

            <div className="lg-text-container">
                <p className="lg-text">
                    A luz geral de um ambiente  aquela luz que tem como objetivo iluminar uniformemente o cmodo, eliminando o mximo possvel sombras que
                    venham a atrapalhar a realizao de tarefas, com a quantidade de energia radiante (luz) ideal para o conforto lumnico do usurio.
                </p>

                <p className="lg-text">
                    Essa quantidade ideal  determinada pelo nvel de iluminncia (lux), nada mais  do que a relao entre o fluxo luminoso da lmpada e a metragem quadrada do ambiente (Lm/m). O nvel de iluminncia  regulamentado pela <b>ABNT - Associao Brasileira de Normas Tcnicas</b> e
                    deve levar em considerao a capacidade visual dos usurios (em termos gerais segue a idade dos usurios) e a refletncia das superfcies
                    (capacidade de refletir a luz das paredes piso e teto).
                </p>

                <p className="lg-text">
                    A luz geral no necessariamente deve estar vinculada a disposio dos mobilirios no cmodo (layout), visto que ela  uma luz para o ambiente.
                    Sombras que a luz geral no conseguiu retirar ou sombras criadas pelo posicionamento do usurio, em virtude da distribuio dos moveis, so
                    eliminadas pela luz de destaque. Essa sim tem relao como layout do ambiente e, normalmente,  direcional., enquanto a luz geral  difusa. Isso
                    deve ajudar a voc a identificar a luz geral do seu cmodo.
                </p>

                <p className="lg-text">
                    Lembre! Ela pode ser ofertada por um ou mais de um conjunto de equipamentos (lmpada e luminria), sempre distribudos uniformemente, por isso, quando  ofertada s por um conjunto de equipamento, esse fica localizado no centro do ambiente.
                </p>

                <p className="lg-text">
                    O Lumos ajuda voc a saber quanto de fluxo luminoso cada lmpada que voc usa para a luz geral de cada cmodo deve ter, considerando a
                    quantidade de pontos de luz do ambiente, assim como a quantidade de lmpada do conjunto de equipamentos utilizado em cada ponto.
                </p>
            </div>
        </div>
    );
};

export default Home;