// src/pages/Faq.js
import arrowLeft from '../Assets/Images/ArrowLeft.png'
import plus from '../Assets/Images/plus.png'
import { useNavigate } from 'react-router-dom';
import React, { useState } from 'react';
import NavBar from "./NavBar/NavBar";
import './Faq.css';

const Faq = () => {
  const [expandedIndex, setExpandedIndex] = useState(null);
  const navigate = useNavigate();

  const toggleExpand = (index) => {
    setExpandedIndex(expandedIndex === index ? null : index);
  };

  const handleBackClick = () => {
    navigate('/home');  // Navega para a rota raiz "/"
  };




  const faqs = [
    {
      question: "As embalagens das lmpadas fornecem as informaes sobre as propriedades lumnicas que aparecem no glossrio do Lumos?", 
      answer: `Informaes que embalagens devem conter so regulamentadas no Brasil pelo INMETRO - Instituto Nacional de Metrologia, Qualidade e Tecnologia. Atualmente, as portarias emitidas por essa autarquia federal contemplam a obrigatoriedade dessas informaes nas embalagens de lmpadas e isso  seguido  risca pelos fabricantes.  No entanto, ateno! Nem todas as fontes de luz normalmente usadas nas residncias so consideradas lmpadas.  o caso dos painis de LED. LED  Luz emitida por diodos  pode adquirir qualquer formato de bulbo, inclusive assumir a forma de uma luminria. Nesse caso, pode ser que o fabricante no te informe as propriedades, pois  comum a informao s ser dada quando obrigatria.
              O fabricante tambm pode omitir informao no obrigatria, quando ela no  relevante para o produto. O grau de abertura de lmpadas difusas  um exemplo disso. Como no h risco de ofuscamento, a informao no  passada. Acontece, por vezes, em lmpadas com grau igual ou superior a 120.
              O produto tambm pode vir sem essas informaes se no tiver sido produzido no Brasil e no tiver passado pelo processo habitual de importao. Nesse caso, escolha outro produto para garantir a qualidade de seu consumo. De qualquer forma, se as informaes no estiverem nas embalagens, normalmente elas so acessadas no site do fabricante, junto as informaes tcnicas, pois so informaes requeridas pelos projetistas que trabalha com iluminao.`
    },
    {
      question: "Posso escolher as lmpadas por sua eficincia energtica (rendimento luminoso)?",
      answer: "Escolher qualquer fonte de luz apenas por sua eficincia energtica no  o mais recomendado. O produto tem que ser bom para seu bolso e para o nosso meio ambiente, mas tambm para sua sade fsica (viso) e psicolgica, isto , precisa ofertar conforto lumnico. O que garante o conforto lumnico so as propriedades lumnicas da luz que interagem com o ambiente e promovem o estmulo luminoso que  percebido pelo usurio. Alm disso, atualmente, todas as fontes de luz comercializadas no Brasil economizam energia, ento voc no corre o risco de adquirir um equipamento que vai onerar sua despesa com a eletricidade da sua casa ou comprometer a sustentabilidade do planeta. O consumo correto ento  pelas unidades fotomtricas contidas nas embalagens e, caso voc encontre mais de um produto com as especificaes adequadas para seu ambiente, escolha aquele com valor de potncia (watts) mais baixo. Dessa forma voc garante maior eficincia da sua iluminao, incluindo a energtica."
    },
    {
      question: "Por que no encontro mais lmpadas incandescentes para consumo nas lojas?",
      answer: "As Lmpadas incandescentes no so mais encontradas para consumo no Brasil, em virtude da poltica de desenvolvimento sustentvel do Brasil. A Portaria Interministerial MME/MCT/MDI n 1.007, de 31 de dezembro de 2010 passou a regulamentar os nveis mnimos de eficincia energtica de lmpadas incandescentes, estipulados pelo Inmetro - Instituto Nacional de Metrologia, Normalizao e Qualidade Industrial, por meio do Programa Brasileiro de Etiquetagem aplicado a fabricantes e fornecedores desde 1984. Em virtude dessa portaria (BRASIL, 2010), as lmpadas incandescentes no Brasil foram progressivamente deixando de ser fabricadas e importadas entre os anos de 2010 e de 2016, sendo sua comercializao tambm gradativamente extinta at 2017."
    },
    {
      question: "Porque no posso escolher minha fonte de luz apenas pela potncia (watts)? Porque tenho que conhecer as demais unidades fotomtricas da fonte de luz que pretendo adquirir?",
      answer: `Potncia ou Fluxo energtico  a quantidade de energia que fonte de luz precisa para emitir luz.  uma informao muito importante, pois mostra a demanda de instalao do sistema eltrico, bem como a eficincia energtica do equipamento, uma vez que a rao de fluxo luminoso e potencia  a medida de quo bem o equipamento converte a eletricidade para a luz visvel que indica o rendimento luminoso. No entanto, a potncia no fornece dados sobre a luz emitida, A escolha da lmpada por sua potncia tem uma explicao histrica que vamos resumir aqui para voc. 
      No final do sculo XIX e incio do sculo XX, quando a utilizao da iluminao eltrica estava sendo difundida, havia poucas opes de lmpadas, As lmpadas incandescentes eram boas para niveis de luz interior e dominou o mercado por 6 dcadas ou mais. Desde 1879, quando foi inventado por Thomas Edison, at 1930, no havia outro equipamento para o projeto de iluminao interior de qualquer programa, inclusive o residencial. 
      Os equipamentos de fonte primria de luz ofereciam quase o mesmo IRC e Temperatura de cor. Suas formas de bulbo eram quase as mesmas, bem como seu ngulo slido, tendo candela muito semelhante. A nica diferena significativa na forma era entre a lmpada transparente e a lmpada leitosa. O vidro fosco era um recurso para difundir o fluxo luminoso e reduzir o ofuscamento dos usurios que no estavam habituados  intensidade luminosa da luz eltrica. 
      No havia muita diferena nas unidades fotomtricas para o conforto visual, alm da informao sobre o fluxo luminoso que poderia ser indicada, nesse caso, pelo fluxo energtico. Naquela poca, a potncia da lmpada era realmente a informao mais importante. No havia a preocupao com o meio ambiente nem com as emisses de carbono, como hoje, mas o fluxo energtico do equipamento era muito importante para a instalao do sistema eltrico e para o custo monetrio da iluminao, que sempre foi alto. 
      A populao aprendeu a fazer a escolha de seus equipamentos pela potencia da fonte de luz (watts) e esta era a nica informao na embalagem das lmpadas. Era uma relao simples entre oferta e demanda. Ao longo do tempo, a cincia do conforto visual, com o interesse em diminuir o custo monetrio do sistema de iluminao eltrica e a crescente demanda por reduzir o consumo de energia em busca de um desenvolvimento sustentvel, impulsionou pesquisas sobre outras lmpadas e, hoje em dia, h muitos equipamentos que as pessoas podem ter acesso. Diferente da lmpada incandescente, esses produtos possuem muita variao entre si e para escolher a melhor opo  necessrio saber suas unidades fotomtricas que expressam suas propriedades lumnicas.`
    },
    {
      question: "Qual a diferena entre luz geral e luz de destaque?",
      answer: `A luz geral de um ambiente  aquela luz que tem como objetivo iluminar uniformemente o cmodo, eliminando o mximo possvel sombras que venham a atrapalhar a realizao de tarefas, com a quantidade de energia radiante (luz) ideal para o conforto lumnico do usurio. Essa quantidade ideal  determinada pelo nvel de iluminncia (lux), que nada mais  do que a relao entre o fluxo luminoso da lmpada e a metragem quadrada do ambiente (Lm/m). O nvel de iluminncia  regulamentado pela ABNT  Associao Brasileira de Normas Tcnicas e deve levar em considerao a capacidade visual dos usurios (em termos gerais segue a idade dos usurios) e a refletncia das superfcies (capacidade de refletir a luz das paredes, piso e teto). A luz geral no necessariamente deve estar vinculada  disposio dos mobilirios no cmodo (layout), visto que ela  uma luz para o ambiente, e pode ser ofertada por um ou mais de um conjunto de equipamentos (lmpada e luminria), sempre distribudos uniformemente, por isso, quando  ofertada s por um conjunto de equipamento, esse fica localizado no centro do ambiente. O Lumos ajuda voc a saber quanto de fluxo luminoso cada lmpada que voc usa para a luz geral de cada cmodo deve ter, considerando a quantidade de pontos de luz do ambiente, assim como a quantidade de lmpada do conjunto de equipamentos utilizado em cada ponto. Interaja com o Lumos atravs do Light Match.
  A luz de destaque, por sua vez,  a iluminao usada para destacar superfcies e criar um ambiente mais dinmico. Diferente da luz geral, essa iluminao visa conduzir a luz para determinadas superfcies, criando diferena proposital de luminosidade. Ela explora as sombras, dando mais dramaticidade ao local e deve dialogar com o layout (disposio dos mobilirios e peas de adorno). Quando a luz de destaque tem como objetivo eliminar as sombras que a luz geral no conseguiu retirar ou sombras criadas pelo posicionamento do usurio, em virtude da distribuio dos mveis, ou ainda compensar a quantidade de luz necessria para realizao de alguma tarefa de preciso, ela tambm  regulamentada pela ABNT. Nesse caso, as superfcies iluminadas com luz de destaque so planos de trabalho como: bancada da cozinha, mesa do escritrio, rea do espelho do banheiro, entre outros.
  Na luz geral ou na luz de destaque, as luminrias tm um papel fundamental para a iluminao do ambiente junto com as lmpadas. Pode-se dizer que as luminrias da luz geral devem ofertar uma luz difusa, seja ela pendente, de sobrepor ou de embutir, localizada no teto ou nas paredes. J as luminrias de luz de destaque so direcionais e, exatamente por isso, deve-se ter uma preocupao maior com o ofuscamento, sempre protegendo a viso de ter contato direto com a fonte de luz.`
    },
    {
      question: "O aplicativo Lumos ajuda na escolha s da lmpada ou ajuda tambm a escolher a luminria correta para o ambiente?",
      answer: `No. O Lumos ajuda a voc a identificar as unidades fotomtricas que sua fonte de luz deve ter para a luz geral dos cmodos de sua residncia, com base nas propriedades lumnicas ideais. Isso vai ajudar a voc a procurar no mercado uma lmpada que lhe atenda e lhe proporcione maior conforto lumnico, o que normalmente se faz sem ajuda de um profissional, principalmente se tratando da reposio de peas. No entanto, o aplicativo no te fornece a especificao da lmpada, nem da luminria a ser usada.
  Informamos, todavia, que as luminrias tm um papel fundamental para a iluminao do ambiente, junto com as lmpadas. Alm de ornamentar o cmodo, elas so responsveis pela conduo do fluxo luminoso e influenciam tanto na eficincia energtica da fonte de luz, como no ofuscamento que essa pode causar. Recomenda-se que as luminrias da luz geral ofertem uma luz difusa, seja ela pendente, de sobrepor ou de embutir, localizada no teto ou nas paredes. J as luminrias das luzes de destaque, que so mais direcionais, devem proteger a viso de ter contato direto com a fonte de luz. Alm disso, as luminrias precisam suportar a potncia da lmpada e estar adequada  instalao eltrica do local.
  Sempre que possvel, procure um profissional especializado em lighting design para lhe orientar na iluminao de sua residncia, como designers de interiores, arquitetos ou engenheiros.`
    },
    {
      question: "Preciso verificar as propriedades lumnicas quando for comprar um painel de LED?",
      answer: `Muitas pessoas consideram que os painis de LED so luminrias, mas esse equipamento  uma "lmpada" no formato de luminria, isto , emite luz e, por isso, as propriedades lumnicas dessa luz precisam ser verificadas quando o produto for consumido.`    },
    {
      question: "O que  luz geral?",
      answer: `
  A luz geral de um ambiente  aquela luz que tem como objetivo iluminar uniformemente o cmodo, eliminando o mximo possvel sombras que venham a atrapalhar a realizao de tarefas, com a quantidade de energia radiante (luz) ideal para o conforto lumnico do usurio.
  Essa quantidade ideal  determinada pelo nvel de iluminncia (lux), nada mais  do que a relao entre o fluxo luminoso da lmpada e a metragem quadrada do ambiente (Lm/m2). O nvel de iluminncia  regulamentado pela <strong>ABNT - Associao Brasileira de Normas Tcnicas</strong> e deve levar em considerao a capacidade visual dos usurios (em termos gerais segue a idade dos usurios) e a refletncia das superfcies (capacidade de refletir a luz das paredes, piso e teto).

  A luz geral no necessariamente deve estar vinculada  disposio dos mobilirios no cmodo (layout), visto que ela  uma luz para o ambiente. Sombras que a luz geral no conseguiu retirar ou sombras criadas pelo posicionamento do usurio, em virtude da distribuio dos mveis, so eliminadas pela luz de destaque. Essa sim tem relao com o layout do ambiente e, normalmente,  direcional, enquanto a luz geral  difusa. ISSO deve ajudar a voc a identificar a luz geral do seu cmodo.
  Lembre! Ela pode ser ofertada por um ou mais de um conjunto de equipamentos (lmpada e luminria), sempre distribudos uniformemente, por isso, quando  ofertada s por um conjunto de equipamento, esse fica localizado no centro do ambiente.
  O Lumos ajuda voc a saber quanto de fluxo luminoso cada lmpada que voc usa para a luz geral de cada cmodo deve ter, considerando a quantidade de pontos de luz do ambiente, assim como a quantidade de lmpada do conjunto de equipamentos utilizado em cada ponto.
`
    },
    {
      question: "O projeto Atitudes Sustentveis desenvolve projeto de iluminao?",
      answer: `O projeto Atitudes Sustentveis  um projeto de extenso que visa levar o conhecimento gerado em pesquisas e ensinado dentro da Universidade para a comunidade externa da UFRJ, mas ele no funciona como um escritrio tcnico que atenda demanda de projetos particulares. 
      No entanto, se voc  uma instituio que de alguma forma realiza um papel social, entre em contato conosco atravs das nossas redes sociais que esto disponibilizadas aqui no Lumos. O projeto  formado por diversas aes e podemos ver se sua demanda se caracteriza como uma atividade de extenso que possa ser atendida por nossa equipe executora. Atualmente, na rea da iluminao, ofertamos dinmicas, oficinas e palestras. Podemos analisar o desenvolvimento de projeto de luminotcnica institucionais que configurem extenso ou encaminhar essa demanda para projetos que j desenvolvem essa ao.`
    }
  ];

  return (
    <div id="main-screen" className="faq-container">
      <div className="faq-header">
        <div className="back-button-circle" onClick={handleBackClick}>
          <img src={arrowLeft} className="back-button" alt="Voltar" />
        </div>
        <p className="faq-header-title">FAQ</p>
      </div>
      <div id="informations">
        {faqs.map((item, index) => (
          <div key={index} id="row" className="faq-item">
            <div className="faq-question">
              <p className="faq-title-question">{item.question}</p>
              <button
                onClick={() => toggleExpand(index)}
                className="expand-icon"
              >
                <img
                  src={plus}
                  alt="Abrir"
                  className={expandedIndex === index ? "rotated" : ""}
                />
              </button>
            </div>
              <div className={`content ${expandedIndex === index ? 'expanded' : ''}`}>
                {item.answer.split('\n').map((line, lineIndex) => (
                  <p dangerouslySetInnerHTML={{__html: line.trim()}} key={lineIndex}></p> // Renderiza cada linha em um pargrafo
                ))}
              </div>
          </div>
        ))}
      </div>
      <NavBar colorFaq={true} colorGlossario={false} colorHome={false} colorLightMatch={false}/>
    </div>
  );
};

export default Faq;
